$biggest-font: 'Lato-black';
$medium-font: 'Lato';
$smallest-font: 'Lato';
$marker-size: 3vh;



.App {
  text-align: center;
  font: .8rem $smallest-font;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

//components and stuff

#marker {
  width: $marker-size;
}

.navbar-start {
  margin: auto;
  font-family: $smallest-font;
}

#myName {
  font: 4rem $biggest-font;
  letter-spacing: 0rem;
  text-transform: none; 
}

.title {
  font: 1rem $medium-font;
  letter-spacing: .6rem;
  text-transform: uppercase;  
}

.subtitle {
  font-family: $medium-font;
}

